<script>
    import Swiper from "swiper";
    export default {
        name: "invasion",
        components: {

        },
        data() {
            return {

            }
        },
        created(){
            // this.initSwiper();
        },

        mounted(){
            document.getElementsByTagName('html')[0].scrollTop=0;
            var swiper = new Swiper('.swiper-container', {
                pagination: '.swiper-pagination',
                effect: 'coverflow',
                grabCursor: true,
                centeredSlides: true,
                slidesPerView: 'auto',
                coverflow: {
                    rotate: 50,
                    stretch: 0,
                    depth: 100,
                    modifier: 1,
