        background-repeat: no-repeat;
        background-position: center;
        min-height: 380px;
        /*min-width: 100vw;*/
        height: calc(100vw / 2.74286);
        text-align: center;
    }
    .videoBox video{
        min-height: 380px;
        /*min-width: 100vw;*/
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        overflow-x: hidden;
        height: calc(100vw / 2.74286);
    }
